@charset "UTF-8";
* {
  box-sizing: border-box; }

body {
  padding: 0;
  margin: 0;
  font-family: "Roboto", "Arial", sans-serif;
  font-weight: 300;
  font-size: 14px;
  line-height: 20px;
  color: #3c425f; }

.visually-hidden {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

html,
body {
  min-width: 1440px;
  min-height: 980px; }

body {
  /* Временно для сетки */
  background-image: url("https://assets.htmlacademy.ru/content/skills/6/case-01/inde.jpg");
  background-size: 1440px auto;
  background-repeat: no-repeat;
  background-position: top center; }

.container {
  width: 1440px;
  margin: 0 auto; }
